      // Data for the Overview Section
      realestateOverviewContent: {
        problem:
          "When it comes to home search, it is difficult for the users to find the option that more closely matches the most important characteristics. In most cases, in addition to general parameters such as the number of bedrooms, baths, square footage and interior features, the main role in choosing the place of residence plays the area in which the future house is located. What schools, grocery stores, restaurants, parks etc. are nearby? How far is it from the workplace? Is this area safe to live in? Finding this information from different sources, compiling and analyzing it is a difficult and time-consuming task. The other important factor which rarely appears in the house description is the age of the most expensive equipment and house structural elements like the roof. Even though the house might be well kept, the buyer can face the need of the expensive replacements or repairs due to the normal wear and tear of equipment and materials.",
        goal: "The goal of this project is to create a platform that delivers all the essential information users need to make informed decisions about real estate purchases, while also providing relevant search results that match their specific criteria, helping them save time by avoiding the need to sift through hundreds of unsuitable properties.",
      },

      // Data for the User Research Section
      realestatelUserResearchContent: {
        introTitle: "User Research Insights",
        introParagraph:
          "To better understand users’ pain points during this process, I interviewed 10 individuals who were either currently searching for a home or had recently purchased one. The questions focused on the type of information people need to make decisions about relocating and choosing the area where they want to live, as well as their top concerns and priorities.",
        introBulletedList: {
          heading: "Key Insights from User Interviews:",
          items: [
            "9 out of 10 participants said proximity to high-quality schools was a top priority.",
            "6 out of 10 identified crime rates as one of their major concerns.",
            "5 respondents emphasized the importance of being close to stores, workplaces, and entertainment.",
            "6 participants said they were not willing to spend significant money on renovations or repairs in the near future.",
            "7 out of 10 wanted a complete mortgage payment estimate, including additional costs like HOA fees and mortgage insurance, to quickly assess affordability.",
            "5 users expressed interest in data that could indicate the property’s potential as a long-term investment.",
            "3 people expressed concerns about local weather and natural disaster risks.",
          ],
        },
        painPointsTitle: "User Pain Points",
