                  </svg>`,
                  paragraph:
                    "Since school quality is a top priority for most families with children, I added filters that allow users to search for homes based on assigned school ratings. Users can refine their search by setting a minimum rating for a specific school level—for instance, only showing homes near highly rated high schools. For those who already have a specific school in mind—whether preferred or alternative—there’s also an option to search by school name directly. To improve visibility, I included school ratings in the property list view, giving users quick access to important data without the need to apply filters.",
                },
              ],
            },
          },
