`,
                  paragraph:
                    "The Price History section helps users evaluate whether a home is fairly priced by showing recent price trends for similar properties in the area. Users can view a price change chart based on homes sold in the past three to six months, which gives a more accurate picture than active listings alone. A full list of the comparable properties used in the analysis is also available for reference.",
                },
                {
                  paragraph:
                    "Because market trends vary—even within the same city—the tool adjusts for local dynamics, such as neighborhoods undergoing growth or decline. To further support investment-minded buyers, the feature also allows comparison with similar homes in nearby neighborhoods.",
                },
                {
                  paragraph:
                    "Key features in the comparison list are visually highlighted to show how closely each property matches the one being evaluated. This makes it easier for users to assess value at a glance and make competitive, data-backed offers.",
                },
              ],
            },
          },
        ],
      },

